import React from 'react'

export default function Quotes() {
    return (
        <div className="cont" id="quoteP">
            <h1>I am a Doctor Who Fan</h1>
            <style jsx>{`
                @keyframes bgGradient {
                    0% {
                        background-position: 0% 50%;
                    }
                    50% {
                        background-position: 100% 50%;
                    }
                    100% {
                        background-position: 0% 50%;
                    }
                }
                .cont {
                    width: 100%;
                    height: auto;
                    position: relative;
                    display: grid;
                   text-align: center;
                   border-top: 30px solid #010101;
                   color: #ffffff;
                   padding-bottom: 90px;
                   background: linear-gradient(-45deg, #ffff00, #E20338, #2F3538, #0351C1);
                   background-size: 400% 400%;
                   animation: bgGradient 15s infinite;
                }
                h1{
                    margin: 50px;
                }
            `}</style>
        </div>
    )
}
